.beauty-poliycy-box {
  font-size: 14px;
  line-height: 1.6;
  color: #999;
}
.beauty-poliycy-box .bp-declaration,
.beauty-poliycy-box .bp-state,
.beauty-poliycy-box .bp-s-row:not(:first-child) {
  padding-left: 3em;
}
.beauty-poliycy-box .bp-state.active {
  background: #E3F0FF;
  border: 1px solid #B3D7FF;
  border-radius: 20px;
  color: #222;
}
.beauty-poliycy-box .bp-state.active .bp-s-transition {
  color: #EE6723;
}
.beauty-poliycy-box .bp-state .bp-s-event {
  pointer-events: none;
}
.beauty-poliycy-box .bp-state.active .bp-s-event {
  display: inline-block;
  cursor: pointer;
  color: #3e94f3;
  pointer-events: auto;
}
